.svg .button {
  text-decoration: none;
  color: #fff;
  position: relative;
  display: inline-block;
  width: 130px;
  height: 40px;
  margin: 20px;
  overflow: hidden;
}

.svg .button rect {
  position: absolute;
  top: 0;
  left: 0;
  stroke-width: 4px;
  stroke-dashoffset: 400px;
  stroke: rgb(204, 26, 26);
}

.svg .button span {
  color: rgb(0, 0, 0);
  width: 133px;
  height: 50px;
  display: inline-block;
  text-align: center;
  position: absolute;
  left: 0;
  top: 0;
  line-height: 40px;
  transition: all 0.2s linear;
}

.svg .button span:hover {
  color: #fff;
  background: rgb(0, 0, 0);
  transition: all 1s cubic-bezier(1.25s 0, 1.15, 1, 1);
  transition-delay: 0.5s;
}

.svg .button:nth-child(1):hover rect {
  animation: draw1 0.75s linear forwards;
}

.svg .button:nth-child(2):hover rect {
  animation: draw2 0.75s linear forwards;
}

.svg .button:nth-child(3) span {
  border: 3px solid rgb(255, 255, 255);
  transform: scale(1);
  width: 125px;
  height: 35px;
  line-height: 37px;
}

.svg .button:nth-child(3):hover span {
  animation: draw3 0.75s linear forwards;
}

.h5,
h5 {
  font-size: 1.25rem;
  border-bottom: 2px dotted rgb(233, 23, 58);
  width: 300px;
  justify-content: center;
  display: inline-flex;
  margin-bottom: 26px;
  padding: 10px;
}

@keyframes draw1 {
  0% {
    stroke-dasharray: 300;
    stroke-dashoffset: 700;
    stroke-width: 4px;
  }
  75% {
    stroke-dasharray: 900;
    stroke-width: 1px;
  }
  100% {
    stroke-dasharray: 900;
    stroke-width: 1px;
  }
}

@keyframes draw2 {
  0% {
    stroke-dashoffset: 350;
    stroke-dasharray: 350;
  }
  50% {
    stroke-dashoffset: 600;
    stroke-dasharray: 400;
    stroke-width: 1px;
  }
  100% {
    stroke-dashoffset: 900;
    stroke-dasharray: 400;
    stroke-width: 1px;
  }
}

@keyframes draw3 {
  100% {
    transform: scale(1.35);
  }
}
